/* harmony default export */ var post = ({
	name: 'post',
	data: function data() {
		return {
			tableData: [],
			oftenGoods: [],
			type0Goods: [],
			type1Goods: [],
			type2Goods: [],
			type3Goods: [],
			totalcount: "",
			totalmoney: "",
			guadanBolen: false,
			waimaiBolen: false

		};
	},

	created: function created() {
		var self = this;
		//			console.log(111);
		axios_default.a.get("http://jspang.com/DemoApi/oftenGoods.php").then(function (response) {
			//				console.log(response);
			self.oftenGoods = response.data;
		}).catch(function (error) {
			console.log(error);
		});
		axios_default.a.get("http://jspang.com/DemoApi/typeGoods.php").then(function (response) {
			//				console.log(response);
			self.type0Goods = response.data[0];
			self.type1Goods = response.data[1];
			self.type2Goods = response.data[2];
			self.type3Goods = response.data[3];
		});
	},
	mounted: function mounted() {
		var orderHeight = document.body.clientHeight;
		document.getElementById("orderlist").style.height = orderHeight + "px";
	},
	methods: {
		//增加；
		addgoods: function addgoods(goodlist) {
			var self = this;
			var ishave = false;
			//			self.totalcount=0;
			//			self.totalmoney=0;
			for (var i = 0; i < self.tableData.length; i++) {
				//						console.log(goodlist);
				if (goodlist.goodsId == self.tableData[i].goodsId) {
					ishave = true;
				}
			}
			if (ishave) {
				var arr = self.tableData.filter(function (o) {
					return o.goodsId == goodlist.goodsId;
				});
				arr[0].count++;
			} else {
				var newgoods = { goodsId: goodlist.goodsId, goodsName: goodlist.goodsName, price: goodlist.price, count: 1 };
				//					console.log(newgoods);
				self.tableData.push(newgoods);
				//					console.log(self.tableData);
			}
			this.countmoney();
		},
		//			计算数量与金额
		countmoney: function countmoney() {
			var self = this;
			self.totalcount = 0;
			self.totalmoney = 0;
			for (var i = 0; i < self.tableData.length; i++) {
				self.totalcount += self.tableData[i].count;
				self.totalmoney += self.tableData[i].count * self.tableData[i].price;
			}
		},
		delSingleGoods: function delSingleGoods(goods) {
			var self = this;
			console.log(goods);
			this.tableData = this.tableData.filter(function (o) {
				return o.goodsId != goods.goodsId;
			});
			this.countmoney();
		},


		//删除全部；
		deltotal: function deltotal() {
			var self = this;
			self.totalcount = 0;
			self.totalmoney = 0;
			//		      	console.log(22);
			//		      	var self=this;
			self.tableData = [];
			//				this.countmoney();	
		},
		//挂单
		guadan: function guadan() {
			console.log(222);
			this.guadanBolen = true;
		},
		//结账
		checkout: function checkout() {
			if (this.totalcount > 0) {
				this.$message({
					message: '结账成功，您共消费了' + this.totalmoney + "元！",
					type: 'success'
				});
				this.tableData = [];
				this.totalcount = 0;
				this.totalmoney = 0;
			} else {
				this.$message.error('请下单！');
			}
		}
	}
});
