import Vue from 'vue'
import Router from 'vue-router'
import leftnav from '@/components/common/leftnav'
import post from '@/components/page/post'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      components: {
        left:leftnav,
        right:post
      }
    },
  ]
})



