<template>
	<div class="post">
		<el-row >
			<el-col :span="7" id="orderlist">
				<el-tabs>
				    <el-tab-pane label="点餐">
				       	<el-table :data="tableData" border style="width: 100%" >
						    <el-table-column prop="goodsName" label="商品"  ></el-table-column>
						    <el-table-column prop="count" label="数量" width="60"></el-table-column>
						    <el-table-column prop="price" label="金额" width="60"></el-table-column>
						    <el-table-column  label="操作" width="100" fixed="right">
						        <template scope="scope">
						            <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
						            <el-button type="text" size="small" @click="addgoods(scope.row)">增加</el-button>
						        </template>
						    </el-table-column>
						</el-table> 
						<div class="total">
							<span>数量:</span><b v-text="totalcount">0</b>
							<span>合计</span><b v-text="totalmoney">0</b><span>元</span>
						</div>
						<div class="btn">
							<el-button type="warning" @click="guadan()">挂单</el-button>
							<el-button type="danger" @click="deltotal()">删除</el-button>
							<el-button type="success"  @click='checkout()'>结账</el-button>
						</div>
				    </el-tab-pane>
				    <el-tab-pane label="挂单" >
				    	<div v-show="guadanBolen">
					       	<el-table :data="tableData" border style="width: 100%" >
							    <el-table-column prop="goodsName" label="商品"  ></el-table-column>
							    <el-table-column prop="count" label="数量" width="60"></el-table-column>
							    <el-table-column prop="price" label="金额" width="60"></el-table-column>
							    <el-table-column  label="操作" width="100" fixed="right">
							        <template scope="scope">
							            <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
							            <el-button type="text" size="small" @click="addgoods(scope.row)">增加</el-button>
							        </template>
							    </el-table-column>
							</el-table> 
							<div class="total">
								<span>数量:</span><b v-text="totalcount">0</b>
								<span>合计</span><b v-text="totalmoney">0</b><span>元</span>
							</div>
							<div class="btn">
								<el-button type="warning" @click="guadan()">挂单</el-button>
								<el-button type="danger" @click="deltotal()">删除</el-button>
								<el-button type="success"  @click='checkout()'>结账</el-button>
							</div>
						</div>
				    </el-tab-pane>
				    <el-tab-pane label="外卖">
				       	<el-table :data="tableData" border style="width: 100%" >
						    <el-table-column prop="goodsName" label="商品"  ></el-table-column>
						    <el-table-column prop="count" label="数量" width="60"></el-table-column>
						    <el-table-column prop="price" label="金额" width="60"></el-table-column>
						    <el-table-column  label="操作" width="100" fixed="right">
						        <template scope="scope">
						            <el-button type="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
						            <el-button type="text" size="small" @click="addgoods(scope.row)">增加</el-button>
						        </template>
						    </el-table-column>
						</el-table> 
						<div class="total">
							<span>数量:</span><b v-text="totalcount">0</b>
							<span>合计</span><b v-text="totalmoney">0</b><span>元</span>
						</div>
						<div class="btn">
							<el-button type="warning" @click="guadan()">挂单</el-button>
							<el-button type="danger" @click="deltotal()">删除</el-button>
							<el-button type="success"  @click='checkout()'>结账</el-button>
						</div>
				    </el-tab-pane>
				</el-tabs>
				
			</el-col>	
            <el-col :span="15">
             	<div class="title">长点外卖</div>
             	<div class="often-goods-list">
             		<ul>
			            <li v-for="goodlist in oftenGoods" @click="addgoods(goodlist)">
			                <span v-text="goodlist.goodsName">香辣鸡腿堡</span>
			                <span class="o-price" v-text="goodlist.price">￥15元</span>
			            </li>
        			</ul>
             	</div>
				<div class="goods-type">
					<el-tabs class="goods-type-list">
						<el-tab-pane label="汉堡">
							<ul class='cookList'>
							    <li v-for="cooklist in type0Goods" @click="addgoods(cooklist)">
							        <span class="foodImg"><img :src="cooklist.goodsImg" width="100%"></span>
							        <span class="foodName" v-text="cooklist.goodsName">香辣鸡腿堡</span>
							        <span class="foodPrice" v-text="cooklist.price">￥20.00元</span>
							    </li>
							</ul>
						</el-tab-pane>	
						<el-tab-pane label="小吃">
							<ul class='cookList'>
							    <li v-for="cooklist in type1Goods" @click="addgoods(cooklist)">
							        <span class="foodImg"><img :src="cooklist.goodsImg" width="100%"></span>
							        <span class="foodName" v-text="cooklist.goodsName">香辣鸡腿堡</span>
							        <span class="foodPrice" v-text="cooklist.price">￥20.00元</span>
							    </li>
							</ul>
						</el-tab-pane>	
						<el-tab-pane label="饮料">
							<ul class='cookList'>
							    <li v-for="cooklist in type2Goods" @click="addgoods(cooklist)">
							        <span class="foodImg"><img :src="cooklist.goodsImg" width="100%"></span>
							        <span class="foodName" v-text="cooklist.goodsName">香辣鸡腿堡</span>
							        <span class="foodPrice" v-text="cooklist.price">￥20.00元</span>
							    </li>
							</ul>
						</el-tab-pane>	
						<el-tab-pane label="套餐">
							<ul class='cookList'>
							    <li v-for="cooklist in type3Goods" @click="addgoods(cooklist)">
							        <span class="foodImg"><img :src="cooklist.goodsImg" width="100%"></span>
							        <span class="foodName" v-text="cooklist.goodsName">香辣鸡腿堡</span>
							        <span class="foodPrice" v-text="cooklist.price">￥20.00元</span>
							    </li>
							</ul>
						</el-tab-pane>	
					</el-tabs>
				</div>
            </el-col>
		</el-row>
	</div>
</template>

<script>
	import axios from "axios"
	export default{
		name:'post',
		data(){
			return {
				  tableData:[],
				 oftenGoods:[], 
			     type0Goods:[], 
			     type1Goods:[],
			     type2Goods:[],
			     type3Goods:[],
			     totalcount:"",
			     totalmoney:"",
			     guadanBolen:false,
			     waimaiBolen:false,
			      
			}
		},
		created:function(){
			var self=this;
//			console.log(111);
			axios.get("http://jspang.com/DemoApi/oftenGoods.php")
			.then(function(response){
//				console.log(response);
				self.oftenGoods=response.data;
			})
			.catch(function(error){
				console.log(error);
			});
			axios.get("http://jspang.com/DemoApi/typeGoods.php")
			.then(function(response){
//				console.log(response);
				self.type0Goods=response.data[0];
				self.type1Goods=response.data[1];
				self.type2Goods=response.data[2];
				self.type3Goods=response.data[3];
			})
		},
		mounted:function(){
			var orderHeight=document.body.clientHeight;
			document.getElementById("orderlist").style.height=orderHeight+"px";
			
		},
		methods:{
			//增加；
			addgoods:function(goodlist){
			var self=this;
			var ishave=false;
//			self.totalcount=0;
//			self.totalmoney=0;
					for(var i=0 ;i<self.tableData.length;i++){
//						console.log(goodlist);
						if(goodlist.goodsId==self.tableData[i].goodsId){
							ishave=true;	
						}
					}
					if(ishave){
						let arr = self.tableData.filter(o =>o.goodsId == goodlist.goodsId);
                 		arr[0].count++;
					}
					else{
					var newgoods={goodsId:goodlist.goodsId,goodsName: goodlist.goodsName,price:goodlist.price,count:1};
//					console.log(newgoods);
					self.tableData.push(newgoods);
//					console.log(self.tableData);
					
					}
					this.countmoney();
			},
//			计算数量与金额
			countmoney:function(){
				var self=this;
			 	self.totalcount=0;
				self.totalmoney=0;
				for(var i=0 ;i<self.tableData.length;i++){
						self.totalcount+=self.tableData[i].count;
						self.totalmoney+=self.tableData[i].count*self.tableData[i].price;
					}
			},
			 delSingleGoods(goods){
			 	var self=this;
		        console.log(goods);
		        this.tableData=this.tableData.filter(o => o.goodsId !=goods.goodsId);
		 		this.countmoney();
		      },
		      
		      //删除全部；
		      deltotal:function(){
		      	var self=this;
			 	self.totalcount=0;
				self.totalmoney=0;
//		      	console.log(22);
//		      	var self=this;
		      	self.tableData=[];
//				this.countmoney();	
		      },
		      //挂单
		      guadan:function(){
		      	console.log(222);
		      	this.guadanBolen=true;
		      },
		      //结账
		      checkout() {
			    if (this.totalcount>0) {
			        this.$message({
			            message: '结账成功，您共消费了'+this.totalmoney+"元！",
			            type: 'success'
			        });
			       	this.tableData = [];
			        this.totalcount = 0;
			        this.totalmoney = 0;
			 
			    }
			    else{
			        this.$message.error('请下单！');
			    }
 
}

		},
	}
</script>

<style>
	#orderlist{
		box-sizing: border-box;
		background: #eee;
	}
	.btn{
		margin-top: 15px;
	}
 .title{
       height: 20px;
       border-bottom:1px solid #D3DCE6;
       background-color: #F9FAFC;
       padding:10px;
   }
   .often-goods-list{
   	overflow: hidden;
   }
   .often-goods-list ul li{
      list-style: none;
      float:left;
      border:1px solid #E5E9F2;
      padding:10px;
      margin:5px;
      background-color:#fff;
      cursor: pointer;
   }
  .o-price{
      color:#58B7FF; 
   }
   .goods-type-list{
   	text-align: center;
   }
   .cookList li{
       list-style: none;
       width:30%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor: pointer;
 
   }
   .cookList li span{
       
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 16px;
       padding-left: 10px;
       color:brown;
 
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
   .total{
   	background: #00d5dd;
   	padding: 10px 0;
   }
   .total b{
   	margin:0 10px;
   }
   
</style>


